.login-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  
    form {
      display: flex;
      flex-direction: column;
      background-color: #ffffff;
      border-radius: 10px;
      padding: 20px;
      box-shadow: 0 3px 10px rgba(0, 0, 0, 0.2);
  
      h1 {
        font-size: 24px;
        margin-bottom: 20px;
        text-align: center;
      }
  
      label {
        font-size: 16px;
        margin-bottom: 10px;
  
        input {
          border: none;
          border-bottom: 2px solid #cccccc;
          font-size: 16px;
          padding: 5px;
          margin-bottom: 20px;
          transition: border-bottom-color 0.3s ease-in-out;
  
          &:focus {
            border-bottom-color: #4a90e2;
            outline: none;
          }
        }
      }
  
      button {
        background-color: #4a90e2;
        color: #ffffff;
        font-size: 16px;
        padding: 10px;
        border: none;
        border-radius: 5px;
        cursor: pointer;
        transition: background-color 0.3s ease-in-out;
  
        &:hover {
          background-color: #3471b8;
        }
      }
    }
  }
  